.messages-list {
    display: flex;
    flex-direction: column;
}

.messages-list > .message {
    button {
        background-color:rgb(98, 101, 117);
    }
    .control-component {
        min-height: 30px;
    }
    max-width: 400px;
    border: 8px solid #dd59b1;
    padding: 10px;
    display: flex;
    flex-direction: column;

    &.message-custom {
        background-color: rgb(175, 154, 59);
    }

    &.message-multiple {
        background-color: rgb(40, 117, 40);
    }

    &.message-single {
        background-color: rgb(68, 96, 223);
    }

    .content {
        background-color: rgb(156, 151, 151);
    }

    .content-item {
        border: 3px solid orange;
        margin: 5px;
    }

    .btc-amount {
        font-weight: bold;
    }
}
